import React from "react";
import Layout from "./../components/layout/layout";

const About = () => {
  return (
    <Layout title={"Sobre Nosotros - Footie Fuel"}>
      <div
        className="container-fluid row mt-6 home-page background-image2 custom-container"
        style={{
          minHeight: "100vh",
          paddingBottom: "100px",
          paddingTop: "50px",
          margin: 0,
          paddingRight: 0,
          width: "100%",
        }}
      >
        <h1 style={{ color: "white" }}>Sobre Nosotros</h1>
        <p style={{ fontFamily: "Formula1", color: "white" }}>
          Bienvenido a Footie Fuel, tu tienda online de productos de Frmula 1 y
          Ftbol.
        </p>
        <p style={{ fontFamily: "Formula1", color: "white" }}>
          En Footie Fuel nos apasiona el deporte y queremos ofrecerte una amplia
          variedad de productos relacionados con la Frmula 1 y el Ftbol.
        </p>
        <p style={{ fontFamily: "Formula1", color: "white" }}>
          Desde camisetas de tus equipos favoritos hasta rplicas de coches de
          Frmula 1, tenemos todo lo que necesitas para mostrar tu pasin por
          estos deportes.
        </p>
        <p style={{ fontFamily: "Formula1", color: "white" }}>
          Nuestro objetivo es proporcionarte productos de alta calidad y
          brindarte una experiencia de compra excepcional.
        </p>
        <p style={{ fontFamily: "Formula1", color: "white" }}>
          No importa si eres un fantico del ftbol o de la Frmula 1, en Footie
          Fuel encontrars los mejores productos y accesorios para demostrar tu
          amor por estos deportes.
        </p>
        <p style={{ fontFamily: "Formula1", color: "white" }}>
          Explora nuestra tienda en lnea y descubre el combustible perfecto
          para tu pasin deportiva en Footie Fuel!
        </p>
        <div className="fondo-container11 text-center">
          <div className="us">
            <img
              className="us-foto"
              src="/images/sobrenosotros.png"
              alt="us"
              width="480px"
              height="490px"
            />
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default About;
